import React, { useState, useEffect } from "react";
import CompanyInfo from "./../Screens/CompanyInfo"; // Adjust the path as necessary
import companyInformation from "./../core/data"; // Adjust the path as necessary
import SuccessPopupComponent from "./../common/components/SuccessPopup.component";
import CustomerDetail from "./../Screens/CustomerDetail";
import ItemDetails from "./../Screens/Items/ItemDetails";
import Discount from "./../Screens/Discount";
import PricingSummary from "./PricingSummary";
import Footer from "./../Screens/Footer"; // Adjust the path as necessary
import generatePDF from "../core/services/pdf-generator.service";
import "./screen.style.css"; // Import your CSS file for styles

function QuotationForm() {

    // Example state for customer info
    const [customerInfo, setCustomerInfo] = useState({
        name: "",
        phone: "",
        address: "",
    });
    // Change to support multiple items
    const [itemBasicInfoList, setItemBasicInfoList] = useState([
        {
            itemName: "",
            quantity: '',
            amount: '',
            totalPrice: '',
        }
    ]);
    const [itemSpecificationsRowsList, setItemSpecificationsRowsList] = useState([
        [
            { detail: "", value: "" },
            { detail: "", value: "" },
            { detail: "", value: "" },
        ]
    ]);
    const [pricing, setPricing] = useState({
        sgstAmount: 0,
        cgstAmount: 0,
        finalPriceWithGst: 0,
        totalPrice: 0,   // for calculation
    });
    // Update discountInfo to work with first item for now (or you can extend to per-item discounts)
    const [discountInfo, setDiscountInfo] = useState({
        isAnyDiscount: "Already discounted",
        discount: "",
        priceAfterDiscount: ""
    });

    const handleDiscountChange = (e) => {
        const { name, value } = e.target;
        setDiscountInfo((prev) => {
            let val = value;

            // Limit discount to 0–100
            if (name === "discount") {
                val = Math.min(Math.max(Number(value), 0), 100);
            }

            const next = { ...prev, [name]: val };

            if (next.isAnyDiscount === 'Yes') {
                const discountPercent = Number(next.discount || 0);
                const price = itemBasicInfoList[0].totalPrice - (itemBasicInfoList[0].totalPrice * discountPercent) / 100;
                // Ensure two decimal places
                next.priceAfterDiscount = price.toFixed(2);
            } else {
                next.discount = '';
                next.priceAfterDiscount = Number(itemBasicInfoList[0].totalPrice).toFixed(2);
            }
            return next;
        });
    };
    // ...other state for itemDetails, productSpecifications, pricing, terms, modal, etc...

    // Example handler for input change
    const handleCustomerChange = (e) => {
        setCustomerInfo({ ...customerInfo, [e.target.name]: e.target.value });
    };

    // ...handlers for grid add/remove, terms edit/save, calculations...

    // ItemDetails handlers now take an index
    const handleItemValueChange = (itemIdx, e) => {
        const { name, value } = e.target;
        setItemBasicInfoList((prev) => {
            const next = prev.map((item, idx) =>
                idx === itemIdx ? { ...item, [name]: value } : item
            );
            // Recompute totalPrice for this item
            const quantity = next[itemIdx].quantity;
            const amount = next[itemIdx].amount;
            let total = (quantity * amount).toString();
            next[itemIdx].totalPrice = Number(total).toFixed(2);

            // Update discountInfo.priceAfterDiscount for first item only (for now)
            if (itemIdx === 0) {
                setDiscountInfo((prev) => {
                    const discountPercent = Number(prev.discount || 0);
                    const price = total - (total * discountPercent) / 100;
                    return { ...prev, priceAfterDiscount: price.toFixed(2) };
                });
            }
            return next;
        });
    };

    // Item specification handlers now take an item index
    const handleItemSpecificationDeleteRow = (itemIdx, rowIdx) => {
        setItemSpecificationsRowsList((prev) =>
            prev.map((rows, idx) =>
                idx === itemIdx ? rows.filter((_, i) => i !== rowIdx) : rows
            )
        );
    };

    const handleItemSpecificationAddRow = (itemIdx) => {
        setItemSpecificationsRowsList((prev) =>
            prev.map((rows, idx) =>
                idx === itemIdx ? [...rows, { detail: "", value: "" }] : rows
            )
        );
    };

    const handleItemSpecificationChange = (itemIdx, rowIdx, e) => {
        const { name, value } = e.target;
        setItemSpecificationsRowsList((prev) =>
            prev.map((rows, idx) =>
                idx === itemIdx
                    ? rows.map((row, i) =>
                        i === rowIdx ? { ...row, [name]: value } : row
                    )
                    : rows
            )
        );
    };

    // Add more items
    const handleAddMoreItems = () => {
        setItemBasicInfoList((prev) => [
            ...prev,
            { itemName: "", quantity: '', amount: '', totalPrice: '' }
        ]);
        setItemSpecificationsRowsList((prev) => [
            ...prev,
            [
                { detail: "", value: "" },
                { detail: "", value: "" },
                { detail: "", value: "" },
            ]
        ]);
    };

    // PDF generation handler: collect all items
    const handleGeneratePDF = async (e) => {
        e.preventDefault();
        const quotationData = {
            customerInfo,
            items: itemBasicInfoList.map((item, idx) => ({
                itemBasicInfo: item,
                itemSpecifications: itemSpecificationsRowsList[idx]
            })),
            discountInfo,
            pricing,
            companyInformation
        }
        console.log("Quotation Data:", quotationData);
        generatePDF(quotationData);
        // Show modal on success
        setShowModal(true);
    };

    const handleDeleteItemRow = (idx) => {
        setItemBasicInfoList((prev) => prev.filter((_, i) => i !== idx));
        setItemSpecificationsRowsList((prev) => prev.filter((_, i) => i !== idx));
    }

    // Modal state
    const [showModal, setShowModal] = useState(false);

    useEffect(() => {
        // Use first item's totalPrice for discount calculation (for now)
        const basePrice = itemBasicInfoList[0]?.totalPrice || 0;
        const priceAfterDiscount = discountInfo.priceAfterDiscount || basePrice;
        const sgst = priceAfterDiscount * 0.09; // 9%
        const cgst = priceAfterDiscount * 0.09; // 9%
        const finalPrice = Number(priceAfterDiscount) + sgst + cgst;

        setPricing({
            sgstAmount: sgst,
            cgstAmount: cgst,
            finalPriceWithGst: finalPrice.toFixed(2),
            totalPrice: priceAfterDiscount,
        });
    }, [discountInfo.priceAfterDiscount, itemBasicInfoList]);

    return (
        <div className="container">
            <div>
                <div className="row align-items-center">
                    <div className="col-md-12 col-12">
                        {/* <CompanyInfo companyInfo={companyInformation}></CompanyInfo> */}
                    </div>
                </div>
            </div>
            <form onSubmit={handleGeneratePDF} className="bg-white p-4 rounded shadow-sm">
                <CustomerDetail customerInfo={customerInfo} handleCustomerChange={handleCustomerChange}></CustomerDetail>
                {itemBasicInfoList.map((itemBasicInfo, idx) => (
                    <div key={idx} className="item-container">
                        <div className="row">
                            <div className="col-10">
                                <div>Item - {idx + 1}</div>
                            </div>
                            <div className="col-2 text-end">
                                <a
                                    href="#"
                                    style={{ color: "red", fontSize: "14px" }}
                                    onClick={(e) => {
                                        e.preventDefault();
                                        handleDeleteItemRow(idx);
                                    }}
                                >
                                    Delete
                                </a>
                            </div>
                        </div>
                        <ItemDetails
                            itemBasicInfo={itemBasicInfo}
                            rows={itemSpecificationsRowsList[idx]}
                            handleDelete={(rowIdx) => handleItemSpecificationDeleteRow(idx, rowIdx)}
                            handleAdd={() => handleItemSpecificationAddRow(idx)}
                            handleItemValueChange={(e) => handleItemValueChange(idx, e)}
                            handleItemSpecificationChange={(rowIdx, e) => handleItemSpecificationChange(idx, rowIdx, e)}
                        />
                    </div>
                ))}
                {/* Add more items button */}
                <div className="mb-3">
                    <a
                        href="#"
                        className="btn btn-outline-primary w-100"
                        onClick={(e) => {
                            e.preventDefault();
                            handleAddMoreItems();
                        }}
                    >
                        Add more items
                    </a>
                </div>
                <Discount discountInfo={discountInfo} change={handleDiscountChange} />
                <PricingSummary pricingInfo={pricing} ></PricingSummary>
                <div className="col-12">
                    <a
                        href="#"
                        className="btn btn-success w-100"
                        onClick={(e) => {
                            e.preventDefault();
                            handleGeneratePDF(e);
                        }}
                    >
                        Generate PDF
                    </a>
                </div>
            </form>
            <div className="mt-4">
                <Footer companyInfo={companyInformation} />
            </div>

            {/* ...Display section for generated data (if needed)... */}

            {/* Success Modal */}
            {showModal && (
                <SuccessPopupComponent message="Quotation downloaded successfully." onClose={() => setShowModal(false)} />
            )}
        </div>
    );
}

export default QuotationForm;

